<template>
  <v-container fluid>
<!--    <v-select v-model="note" :items="notes" @change="getScaleNotes"></v-select>-->
    <v-row>
      <v-col  cols="2">
        <v-combobox v-model="tuning"
                    :items="Object.keys(stringTunings)"
                    label="Tuning" chips
                    outlined
                    attach
                    @change="initialize"></v-combobox>
      </v-col>
      <v-col  cols="3">
        <div  class="v-input v-input--is-label-active v-input--is-dirty theme--light v-text-field v-text-field--is-booted v-text-field--enclosed v-text-field--outlined v-select v-select--chips v-autocomplete">
<div class="v-input__control">
          <div class="v-input__slot">
        <fieldset aria-hidden="true">

            <legend style="width: 64px;"><span>&#8203;</span></legend>
        </fieldset>
          <div class="v-select__slot">
            <label for="noteInput" class="v-label v-label--active theme--light" style="left: 0px; right: auto; position: absolute;">
              Root Note</label>
        <v-item-group class="mt-2" id="noteInput" v-model="key.note" @change="getScaleNotes">
                <v-item v-for="n in 12" :key="notes[n-1]" class="mx-1"
                        v-bind:class="{
                          'mt-8': !notes[n-1].endsWith('#'),
                          'mt-n8': notes[n-1].endsWith('#'),
                          'mx-n5': notes[n-1].endsWith('#')}"
                    :value="notes[n-1]"
                    v-slot="{ active, toggle }"
                >
                  <v-chip
                      active-class="deep-purple--text text--accent-4"
                      :input-value="active"
                      @click="toggle"
                  >
                    {{ notes[n-1] }}
                  </v-chip>
                </v-item>

        </v-item-group>
          </div>
        </div>
        </div>
        </div>
      </v-col>
      <v-col  cols="2">
        <v-combobox v-model="key.scale"
                    :items="scales"
                    label="Scale" chips
                    outlined
                    attach
                    @change="getScaleNotes"></v-combobox>
      </v-col>

      <v-col  cols="3">
        <div  class="v-input v-input--is-label-active v-input--is-dirty theme--light v-text-field v-text-field--is-booted v-text-field--enclosed v-text-field--outlined v-select v-select--chips v-autocomplete">
          <div class="v-input__control">
            <div class="v-input__slot">
              <fieldset aria-hidden="true">

                <legend style="width: 35.25px;"><span>&#8203;</span></legend>
              </fieldset>
              <div class="v-select__slot">
                <label for="noteInput" class="v-label v-label--active theme--light" style="left: 0px; right: auto; position: absolute;">
                  Chord</label>
                <v-item-group class="mt-2" id="chordInput" v-model="chord" @change="getChordNotes">
                  <v-item v-for="(chord,index) in keyData.chords" v-bind:key="index"
                          :value="chord"
                          v-slot="{ active, toggle }"
                  >
                    <v-chip
                        active-class="deep-purple--text text--accent-4"
                        :input-value="active"
                        @click="toggle"
                    >
                      {{ chord }}
                    </v-chip>
                  </v-item>

                </v-item-group>
              </div>
            </div>
          </div>
        </div>
      </v-col>

    </v-row>

  <div class="fretboard">
    <div class="string" v-for="(string, stringIndex) in strings" v-bind:key="stringIndex">
      <div class="fret" v-for="(fretNote, fretIndex) in string" v-bind:key="fretIndex">
        <div v-if="stringIndex==0 && [3,5,7,9].includes(fretIndex)"  class="fretmark single-fretmark" :style="'top:'+strings.length*20+'px'"></div>
        <div v-if="stringIndex==0 && [12].includes(fretIndex)"  class="fretmark double-fretmark" :style="'top:'+((strings.length*20)-52)+'px'"></div>
        <div class="note"
             :class="{'note-on':fretNote._disp_interval,
             'note-highlight1':fretNote._disp_highlight}"
        @click="playTone(fretNote)"
        >
          <span v-if="!chord && fretNote._disp_interval">{{fretNote._disp_interval}}</span>
<!--          <span v-if="!fretNote._disp_chordInterval">{{fretNote._disp_degree}}{{fretNote._disp_note}}</span>-->
          <span v-if="chord && fretNote._disp_chordInterval">{{fretNote._disp_chordInterval}}</span>
<!--          {{calcNote(fretNote)}}-->
        </div>
      </div>
    </div>
  </div>
<Beats></Beats>
  </v-container>
</template>

<script>
const { Note, Interval, Key, Chord, Scale } = require("@tonaljs/tonal");

import Beats from "@/components/Beats";

export default {
  name: "Fretboard",
  components: {Beats},
  data: function(){
    return {
      stringTunings:{'guitar':[
        {note:'E',octave:5},
        {note:'B',octave:4},
        {note:'G',octave:4},
        {note:'D',octave:4},
        {note:'A',octave:3},
        {note:'E',octave:3}
      ],
        'banjo':[
          {note:'D',octave:4},
          {note:'B',octave:3},
          {note:'G',octave:3},
          {note:'D',octave:3},
          {note:'G',octave:4}
        ],
        'bass ukulele':[
          {note:'G',octave:4},
          {note:'D',octave:4},
          {note:'A',octave:3},
          {note:'E',octave:3}
        ],
        'soprano ukulele':[
          {note:'A',octave:4},
          {note:'E',octave:4},
          {note:'C',octave:4},
          {note:'G',octave:3}
        ]
      },
      tuning:'guitar',
      startFret:0,
      endFret:18,
      strings:[],
      scaleNotes:[],
      key:{
        scale:'major',
        note:'C'
      },
      scales:['major','minor'],
      keyData:{},
      chord:null,
    }
  },
  computed:{
    // scales(){
    //   let scales= Scale.names();
    //   scales.sort();
    //   return scales;
    // },
    notes(){
      return ['C','C#','D','D#','E','F','F#','G','G#','A','A#','B'];
      // Note.names();
    },
  },
  methods:{
    async playTone(note) {
      //create a synth and connect it to the main output (your speakers)
      if (!this.synth) {
        const Tone = await import('tone');
        this.synth = new Tone.Synth().toDestination();
      }
      this.synth.triggerAttackRelease(note.name, "8n");
    },
    getChordNotes(){
      console.log(this.chord);
      let chord = Chord.get(this.chord);
      console.log(chord);
      this.strings.forEach(string=>string.forEach((fretNote)=> {
        fretNote._disp_highlight = false;
        fretNote._disp_chordInterval='';
        chord.notes.forEach((chordNote, index) => {
          if (Note.get(chordNote).chroma === fretNote.chroma && index<3) {
            fretNote._disp_highlight = true;
            fretNote._disp_chordInterval = chord.intervals[index];
          }
        })
      }));
    },
    getScaleNotes(){
      this.chord=null;
      this.strings.forEach(string=>string.forEach((fretNote)=> {
        fretNote._disp_note = '';
        fretNote._disp_degree = '';
        fretNote._disp_highlight = '';
        fretNote._disp_chordInterval='';
        fretNote._disp_interval='';
      }));
      if (this.key.note && this.key.scale){
        if(this.key.scale==="major"){
          this.keyData = Key.majorKey(this.key.note);
        }else if(this.key.scale.startsWith("minor")){
          this.keyData = Key.minorKey(this.key.note).natural;
        }
        this.scaleNotes=[];
        let keyData = this.keyData;

        //Find other chords
        console.log(Scale.scaleChords(keyData.type));
        for(let i=0;i<this.keyData.chords.length;i++){
          this.keyData.chords[i]=this.keyData.chords[i].replace(/(m7b5)/g,'dim').replace(/(maj)?7/g,'');
        }


        this.keyData.scale.forEach(scaleNote => this.scaleNotes.push(Note.get(scaleNote)));
        this.strings.forEach(string=>string.forEach((fretNote)=>{
          this.scaleNotes.forEach((scaleNote, index)=>{
            if(scaleNote.chroma===fretNote.chroma){
              fretNote._disp_note=scaleNote.pc
              fretNote._disp_degree=(index+1)
              fretNote._disp_interval=keyData.intervals[index];
              // fretNote._disp_highlight= index%2==1;
            }
          })
        }));
        console.log(this.scaleNotes)
      }
    },
    getFretNotes(){
      this.strings=[];
      this.stringTunings[this.tuning].forEach((tuning) => {
            let frets = [];
            for(let i=0;i<(this.endFret-this.startFret);i++){
              let semitones = Interval.fromSemitones(i);
              let transposed = Note.get(Note.transpose(tuning.note+tuning.octave, semitones))
              frets.push(transposed)
            }
            this.strings.push(frets);
          }
      )
    },
    initialize(){
      this.getFretNotes();
      this.getScaleNotes();
    }
  },
  created: function(){
    this.initialize();
  }

}
</script>

